        <span>{{user_birthday}}</span>
      </div>
      <div class="user-item">
        <span></span>
        <span><input type="text" v-model="user_sign"></span>
      </div>

      <button @click="saveUserInfo"></button>
    </div>
    <!---->
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
    <!---->
    <mt-datetime-picker
      ref="picker"
      type="date"
      year-format="{value} "
      month-format="{value} "
      date-format="{value} "
      :startDate=startDate
      :endDate=endDate
      @confirm="handleBirthday"
    >
    </mt-datetime-picker>
  </div>
</template>

