        console.log(date);
        // console.log(moment(date).format("YYYYMMDD"));
        // this.user_birthday = moment(date).format("YYYYMMDD");
      },

      // 3. 
      async saveUserInfo(){
          // 3.1 
          // let result = await changeUserInfo(this.userInfo.id, this.user_name, this.user_sex, this.user_address, this.user_birthday, this.user_sign);
         // console.log(result);
         // 3.2 
        Toast({
           message: '',
           position: 'bottom',
           duration: 2000
        });
        setTimeout(()=>{
          this.$router.replace('/me');
        }, 2000)
        // 3.3 
        // if(result.success_code === 200){
        //     // 3.4 
        //     this.$store.dispatch('getUserInfo');
        //     // 3.5 
        //     setTimeout(()=>{
        //        this.$router.replace('/me');
        //     }, 2000)
        // }
      }
    }
  }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .user-detail
    width 100%
    height 100%
    .user-detail-top
      width 100%
      height 60px
      line-height 60px
      padding-left 10px
      font-weight bold
    .user-detail-group
      .user-icon
        height 60px
        padding 0 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
        img
          width 50px
          border-radius 50%
      .user-item
        height 40px
        padding 0 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
        input
          border none
          outline none
          text-align right
      button
        width 90%
        height 40px
        line-height 40px
        background-color #e9232c
        text-align center
        margin 20px 5%
        border none
        font-size 16px
        color #fff
        border-radius 10px
    .right-title-color
